/* common CSS */
:root {
  --bg: #FBFBFC;
  --calvino-red: tomato;
  --dark-green: black;
  --cool-bg-bg: #F3F1EC;
  --cool-bg: #F1F1F5;
  --dark-blue: #5151FC;
  --small-text: #3f3e4c;
  --secondary-element: #C6CACF;
  --header-height: 66px;
  --navigation-height: 58px;
  --navigation-height-by-2: calc(var(--navigation-height) * 2);
  --app-border-top: 6px;
  --navigation-borders: black;
  --text-color: black;
  --app-border: 1px solid black;
  --app-border-bold: 2px solid black;
  --button-border: 2px solid var(--dark-blue);
  --text-size: 14px;
  --main-menu: var(--dimGold);
  font-size: 14px;
  font-family: 'HKGrotesk', sans-serif;
}

title {
  text-transform: uppercase;
}

.col-lg-8 {
  padding-left: 0px;
}

/* ELEMENTI COMUNI */
a {
  text-decoration: none;
  color: var(--text-color);
}

.link {
  color: var(--dark-blue);
}

a.link:hover {
  color: var(--text-color);
}

p {
  font-size: 1.4em;
  line-height: 1.4em;
}

p.intro {
  font-size: 1.7em;
  line-height: 1.5em;
  margin-bottom: 0 !important;
}

strong {
  font-weight: 600;
}

::selection {
  color: var(--bg);
  background-color: var(--dark-blue);
}

a:hover {
  text-decoration: none;
  color: var(--dark-blue);
  transition: color 0.3s ease;
}

a:focus {
  text-decoration: underline;
  color: var(--dark-blue);
  transition: color 0.5s ease;
}

html, body {
  height: 100vh;
}

body {
  margin: 0;
  font-family: "HKGrotesk", sans-serif;
  overflow-x: hidden;
  background-color: var(--bg);
}

h1 {
  margin: 0;
  font-size: 2.4em;
  color: var(--text-color);
  margin-bottom: 1em;
}

h2 {
  margin: 0;
  font-size: 1.8em;
  color: black;
}

h3 {
  margin: 0;
  font-size: 1.5em;
  color: black;
}

h4 {
  margin: 0;
  font-size: 1.3em;
  color: black;
}

h5 {
  margin: 0;
  font-size: 1.2em;
  font-weight: 100;
  color: var(--small-text);
  line-height: 1.4em;
}

.main {
  width: 100vw;
  height: 100vh;
  position: relative;
}

ol {
  list-style-type: decimal;
  list-style-position: outside;
  padding-left: 0px;
  color: var(--small-text);
  font-size: 1.1em;
}

ul {
  list-style-type: none;
  padding-left: 0px;
}

li {
  margin-bottom: 0.5em;
  margin-top: 0.5em;
  padding-left: 0px;
  font-size: 1.1em;
  color: var(--list);
}

.navigations {
  border-bottom: 2px solid var(--navigation-borders);
  box-sizing: border-box;
  height: var(--navigation-height);
  width: 100vw;
  display: grid;
  grid-template-columns: repeat(24, 1fr);
  z-index: 10000;
  position: fixed;
}

.navigations>*:not(:last-child) {
  border-right: 1px solid var(--navigation-borders);
}

.navigations>div {
  max-height: 100%;
  display: flex;
  align-items: center;
}

.page-title, .options-container>.dropdown>button, .search-component {
  padding: 0 10px;
}

.navigations>div svg {}

.top-nav {
  position: sticky;
  background-color: var(--bg);
  top: 0;
}

.the-body-viz {
  height: calc(100vh - var(--navigation-height-by-2));
  display: flex;
}

.bottom-nav {
  position: fixed;
  background-color: var(--bg);
  bottom: 0;
  border-top: 2px solid var(--navigation-borders);
  transition: bottom 0.5s ease;
}

.viz-title {
  text-transform: uppercase;
  font-size: 1.2857142857rem;
}

.pubblication-item {
  margin-bottom: 4rem;
}

.pubblication-item h5 {
  color: black !important;
}

.pubblication-item h3 {
  color: black !important;
}

.pubblication-abstract {
  margin-top: 1rem;
}

/*BUTTON*/
.button-text {
  border: var(--button-border);
  margin-top: 3em;
  padding: 0.5em 1.3em 0.5em 1.3em;
  font-size: 1em;
  font-weight: 500;
  color: var(--dark-blue);
  background-color: var(--bg);
  text-transform: uppercase;
  transition: color 0.3s ease;
  text-decoration: none;
}

a.button-text, a:visited.button-text {
  text-decoration: none;
}

.button-text:hover {
  background-color: var(--dark-blue);
  color: var(--bg);
}

/* BOTTONI DELLE VIZ */
.btn-primary {
  font-weight: 600;
}

.btn-primary a:hover {
  font-weight: 600;
  background-color: var(--dark-blue);
}

/* FOOTER */
footer {
  background-color: #FFA500;
  color: black;
  position: fixed;
  bottom: 0;
  width: 100%;
  font-size: 0.7em;
  padding: 1.5em;
  height: var(--navigation-height);
}

#loghi {
  overflow-y: hidden;
}

#logo-unige {
  width: 100%;
}

#logo-polimi {
  width: 82%;
}

#logo-dd {
  width: 30%;
}

#timeline .selection {
  fill: transparent;
  fill-opacity: 1;
  stroke: var(--text-color);
  stroke-width: 1;
}

#timeline .handle {
  fill: var(--text-color);
  height: 20px;
  opacity: 1;
  stroke: none;
  stroke-width: 5px;
  stroke: transparent;
}

.dropdown-menu * {
  border-radius: 0px;
  height: auto;
  z-index: 20;
  grid-column: 1 / span 3;
  font-size: 1.1em;
  position: relative;
  text-transform: uppercase;
  border-radius: 0px;
  align-content: flex-end;
}

.dropdown-menu {
  height: auto;
  border-radius: 0px;
  border: 1px solid black;
  background-color: var(--bg);
  padding-top: 0em;
  padding-bottom: 0em;
  float: left;
}

.dropdown-item {
  font-size: 1em;
}

.dropdown-menu a:hover {
  cursor: pointer;
  text-decoration: none;
  transition: color 0.3s ease;
}

.dropdown-item.active, .dropdown-item:active {
  color: var(--bg);
  background-color: black;
}

*/ */ .references {
  margin-top: 2em;
  font-style: italic;
  font-size: 1em;
}

.references p {
  margin-top: 1em;
  font-style: italic;
  font-size: 1em;
}

.svg-inline--fa {
  font-size: 1.1em;
}

#overlay img {
  display: block;
  position: absolute;
  z-index: -9999;
  width:15%;
  opacity:0.5;
  /* filter: invert(12%) sepia(67%) saturate(6275%) hue-rotate(237deg) brightness(105%) contrast(98%); */
}

#overlay .floatLeft {
  animation: floatLeft 5s infinite;
  animation-direction: normal;
  height: auto;
}

@keyframes floatLeft {
  0% {
    left: 50px;
    -webkit-animation-timing-function: ease-out;
  }

  50% {
    left: 100px;
    -webkit-animation-timing-function: ease-in;
  }

  100% {
    left: 50px;
    animation-timing-function: ease-out;
  }
}

#overlay .floatTop {
  animation: floatTop 5s infinite;
  animation-direction: normal;
  height: auto;
}

@keyframes floatTop {
  0% {
    top: 800px;
    left: 500px;
    -webkit-animation-timing-function: ease-out;
  }

  50% {
    top: 850px;
    left: 500px;
    -webkit-animation-timing-function: ease-in;
  }

  100% {
    top: 800px;
    left: 500px;
    animation-timing-function: ease-out;
  }
}


#overlay .sizein {
  animation: sizein 2s infinite;
  animation-direction:alternate;
  height: auto;
}

@keyframes sizein {
  0% {
    width: 10%;
    left: 500px;
    top: 500px;
    -webkit-animation-timing-function: ease-out;
  }

  50% {
    width: 11%;
    left: 500px;
    top: 500px;
    -webkit-animation-timing-function: ease-out;
  }

  100% {
    width:10%;
    left: 500px;
    top: 500px;
    -webkit-animation-timing-function: ease-out;
  }
}

#overlay .fade {
  animation: fade 0.8s infinite;
  animation-direction: normal;
  height: auto;
}

@keyframes fade {
  0% {
    opacity: 100%;
    -webkit-animation-timing-function: ease-out;
  }

  50% {
    opacity: 5%;
    -webkit-animation-timing-function: ease-out;
  }

  100% {
    opacity:100%;
    -webkit-animation-timing-function: ease-out;
  }
}



